#full-menu {
  display: block;
  background: {
    color: $color-primary; }
  width: 100%;
  height: 0;
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  text-align: center;
  overflow: hidden;

  &.not-active {
    @include animation(slide-up .4s ease forwards);

    ul {
      top: -1000px; } }

  &.active {
    @include animation(slide-down .4s ease forwards);

    ul {
      @include transition(top .4s .3s);
      top: 0; } }

  ul {
    margin: {
      top: 150px; }
    width: 100%;
    position: absolute;

    @media #{$xsmall-max} {
      margin: {
        top: 90px; } }

    li {
      padding: 15px;
      width: 50%;
      margin: 0 auto;
      font: {
        weight: 600;
        size: 1.3em; }

      @media #{$xsmall-max} {
        width: 90%;
        padding: 5px; }

      span {
        display: block;
        margin: 10px auto;
        width: 0;
        height: 2px;
        background: {
          color: $color-primary-dark; } }

      a {
        color: $color-secondary; }

      &:hover {
        a {
          @include transition(color .2s);
          color: $color-special; }

        span {
          @include transition(width .2s);
          width: 40%;
          background: {
            color: $color-special; } } } } } }

@keyframes slide-down {
  0% {
    height: 0; }
  100% {
    height: 100%; } }

@keyframes slide-up {
  0% {
    height: 100%; }
  100% {
    height: 0; } }
